/** Media Queries **/
@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }

  .tagline,
.formulario label,
.login .acciones p,
.crear .acciones p,
.olvide .acciones p {
    color: #FFFFFF;
  }

  .descripcion-pagina,
.formulario .campo input,
.nombre-pagina, .no-proyectos, .no-tareas {
    color: #f3f4f6;
  }

  .formulario input:not([type=submit]) {
    background-color: #21212c;
  }
  .formulario input:not([type=submit])::placeholder {
    color: #a6a6a6;
  }

  .alertas.error {
    background-color: #FFFFFF;
  }

  .sidebar, .sidebar.Perfil, .sidebar.Proyectos, .sidebar.Crear, .sidebar.Cambiar, .sidebar.mostrar {
    background: linear-gradient(180deg, #2c2c3b 0, #23272e);
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .sidebar, .sidebar.Perfil, .sidebar.Proyectos, .sidebar.Crear, .sidebar.Cambiar, .sidebar.mostrar {
    background: linear-gradient(to bottom, #21212c 0%, #21212c 100%);
  }
}
@media (prefers-color-scheme: dark) {
  .sidebar .sidebar-nav, .sidebar.Perfil .sidebar-nav, .sidebar.Proyectos .sidebar-nav, .sidebar.Crear .sidebar-nav, .sidebar.Cambiar .sidebar-nav, .sidebar.mostrar .sidebar-nav {
    margin-top: 5rem;
  }
  .sidebar .sidebar-nav a, .sidebar.Perfil .sidebar-nav a, .sidebar.Proyectos .sidebar-nav a, .sidebar.Crear .sidebar-nav a, .sidebar.Cambiar .sidebar-nav a, .sidebar.mostrar .sidebar-nav a {
    display: block;
    margin-bottom: 2rem;
    color: #FFFFFF;
    padding: 0.7rem;
    transition: all 300ms ease;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2.4rem;
    text-align: center;
  }
  .sidebar .sidebar-nav a:hover, .sidebar.Perfil .sidebar-nav a:hover, .sidebar.Proyectos .sidebar-nav a:hover, .sidebar.Crear .sidebar-nav a:hover, .sidebar.Cambiar .sidebar-nav a:hover, .sidebar.mostrar .sidebar-nav a:hover {
    background-color: #5b01d8;
    border-radius: 0.5rem;
  }
  .sidebar .sidebar-nav a.activo, .sidebar.Perfil .sidebar-nav a.activo, .sidebar.Proyectos .sidebar-nav a.activo, .sidebar.Crear .sidebar-nav a.activo, .sidebar.Cambiar .sidebar-nav a.activo, .sidebar.mostrar .sidebar-nav a.activo {
    background-color: #5b01d8;
    border-radius: 0.5rem;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .sidebar .sidebar-nav a, .sidebar.Perfil .sidebar-nav a, .sidebar.Proyectos .sidebar-nav a, .sidebar.Crear .sidebar-nav a, .sidebar.Cambiar .sidebar-nav a, .sidebar.mostrar .sidebar-nav a {
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
@media (prefers-color-scheme: dark) {
  .barra-mobile.Proyectos, .barra-mobile.Perfil, .barra-mobile.Cambiar, .barra-mobile {
    background-color: #21212c;
  }
}
@media (prefers-color-scheme: dark) {
  .barra, .barra.Perfil, .barra.Proyectos, .barra.Crear, .barra.Cambiar {
    background-color: #21212c;
  }
  .barra p, .barra.Perfil p, .barra.Proyectos p, .barra.Crear p, .barra.Cambiar p {
    color: #6601F1;
  }
  .barra p span, .barra.Perfil p span, .barra.Proyectos p span, .barra.Crear p span, .barra.Cambiar p span {
    color: #FFFFFF;
  }
  .barra .cerrar-sesion, .barra.Perfil .cerrar-sesion, .barra.Proyectos .cerrar-sesion, .barra.Crear .cerrar-sesion, .barra.Cambiar .cerrar-sesion {
    background-color: #6601F1;
    padding: 1.5rem 4rem;
    color: #FFFFFF;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .barra .cerrar-sesion, .barra.Perfil .cerrar-sesion, .barra.Proyectos .cerrar-sesion, .barra.Crear .cerrar-sesion, .barra.Cambiar .cerrar-sesion {
    width: auto;
  }
}
@media (prefers-color-scheme: dark) {
  .barra .cerrar-sesion:hover, .barra.Perfil .cerrar-sesion:hover, .barra.Proyectos .cerrar-sesion:hover, .barra.Crear .cerrar-sesion:hover, .barra.Cambiar .cerrar-sesion:hover {
    background-color: #5101be;
    cursor: pointer;
  }
}
@media (prefers-color-scheme: dark) {
  .dashboard {
    background-color: #2c2c3b;
  }
  .dashboard .enlace {
    color: #f3f4f6;
  }
  .dashboard .enlace:hover {
    color: #d6d9e0;
  }
  .dashboard .formulario input:not([type=submit]) {
    background-color: #21212c;
  }
  .dashboard .formulario input:not([type=submit])::placeholder {
    color: #a6a6a6;
  }
  .dashboard .formulario .guardar-perfil, .dashboard .formulario .crear-proyecto {
    background-color: #6601F1;
    padding: 1.5rem 4rem;
    color: #FFFFFF;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .dashboard .formulario .guardar-perfil, .dashboard .formulario .crear-proyecto {
    width: auto;
  }
}
@media (prefers-color-scheme: dark) {
  .dashboard .formulario .guardar-perfil:hover, .dashboard .formulario .crear-proyecto:hover {
    background-color: #5101be;
    cursor: pointer;
  }
}
@media (prefers-color-scheme: dark) {
  .dashboard .modal .formulario {
    background-color: #2c2c3b;
  }
  .dashboard .modal .formulario legend {
    color: #f3f4f6;
  }
}
@media (prefers-color-scheme: dark) {
  .dashboard .modal .formulario .submit-nueva-tarea, .dashboard .agregar-tarea {
    background-color: #6601F1;
  }
}
@media (prefers-color-scheme: dark) {
  .listado-proyectos .proyecto {
    background: linear-gradient(90deg, #21212c 0, #37374a);
  }
}
@media (prefers-color-scheme: dark) {
  .listado-tareas .tarea {
    background: #424258;
    border: none;
    border-radius: 0.5rem;
    padding: 0 2rem;
  }
  .listado-tareas .tarea p {
    color: #FFFFFF;
  }
}
@media (prefers-color-scheme: dark) {
  .filtros {
    background-color: #424258;
  }
  .filtros h2, .filtros label {
    color: #FFFFFF;
  }
  .filtros input[type=radio] {
    accent-color: #F59E0B;
  }
}